html,
body {
  height: 100%;
  overflow: hidden;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
}
#root {
  height: 100%;
}
.home-wrapper {
  background-color: #fff;
}
.home-wrapper .carousel-wrapper {
  padding: 15px;
}
.home-wrapper .carousel-wrapper .am-carousel {
  border-radius: 5px;
  overflow: hidden;
}
.home-wrapper .carousel-wrapper .am-carousel .carousel-slider-slide-a {
  display: inline-block;
  width: 100%;
  height: 150px;
}
.home-wrapper .carousel-wrapper .am-carousel .carousel-slider-slide-a img {
  width: 100%;
  height: 150px;
}
.home-wrapper .carousel-wrapper .am-carousel .am-carousel-wrap {
  padding-bottom: 2px;
}
.home-wrapper .carousel-wrapper .am-carousel .am-carousel-wrap .am-carousel-wrap-dot > span {
  width: 6px;
  height: 6px;
  background: rgba(255, 255, 255, 0.6);
}
.home-wrapper .carousel-wrapper .am-carousel .am-carousel-wrap .am-carousel-wrap-dot-active > span {
  background: #d43c33;
}
.home-wrapper .entrance-list-wrapper {
  padding: 0 15px 15px 15px;
}
.home-wrapper .entrance-list-wrapper .entrance-list {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.home-wrapper .entrance-list-wrapper .entrance-list li {
  text-align: center;
}
.home-wrapper .entrance-list-wrapper .entrance-list li > a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.home-wrapper .entrance-list-wrapper .entrance-list li > a .entrance-list-icon {
  display: inline-block;
  background-color: #d43c33;
  width: 44px;
  height: 44px;
  line-height: 44px;
  text-align: center;
  border-radius: 50%;
}
.home-wrapper .entrance-list-wrapper .entrance-list li > a .entrance-list-icon i {
  color: #fff;
  font-size: 20px;
}
.home-wrapper .entrance-list-wrapper .entrance-list li > a p {
  padding-top: 8px;
  font-size: 12px;
  color: #79797B;
}
.home-wrapper .remd-songlist-wrapper {
  padding: 0 15px;
}
.home-wrapper .remd-songlist-wrapper h1 {
  margin: 15px 0;
  font-size: 20px;
  font-weight: 600;
}
.home-wrapper .remd-songlist-wrapper ul {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
}
.home-wrapper .remd-songlist-wrapper ul li {
  margin-bottom: 15px;
}
