.songlist-wrapper {
  width: 105px;
}
.songlist-wrapper .songlist-cover {
  width: 100%;
  height: 105px;
  border-radius: 5px;
}
.songlist-wrapper .songlist-txt {
  padding-top: 4px;
  font-size: 12px;
  color: #000;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-height: 16px;
}
