html,
body {
  height: 100%;
  overflow: hidden;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
}
#root {
  height: 100%;
}
.navbar-wrapper {
  width: 100%;
  background-color: #fff;
}
.navbar-wrapper .navbar {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.navbar-wrapper .navbar .navbar-item {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1;
      -ms-flex: 1 1;
          flex: 1 1;
  text-align: center;
}
.navbar-wrapper .navbar .navbar-item.active .navbar-item-icon {
  background-color: #d43c33;
  color: #fff;
}
.navbar-wrapper .navbar .navbar-item.active .navbar-item-name {
  color: #d43c33;
}
.navbar-wrapper .navbar .navbar-item .navbar-item-icon {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  display: inline-block;
  border-radius: 50%;
  padding: 6px;
  color: #918C86;
}
.navbar-wrapper .navbar .navbar-item .navbar-item-icon i {
  font-size: 20px;
}
.navbar-wrapper .navbar .navbar-item .navbar-item-name {
  padding-top: 5px;
  color: #918C86;
  font-size: 12px;
}
