html,
body {
  height: 100%;
  overflow: hidden;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
}
#root {
  height: 100%;
}
.appbar-wrapper {
  height: 45px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #fff;
}
.appbar-wrapper .full-width {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1;
      -ms-flex: 1 1;
          flex: 1 1;
}
.appbar-wrapper .icon-btn {
  padding: 0 15px;
}
.appbar-wrapper .icon-btn i {
  color: #000;
  font-size: 18px;
}
.appbar-wrapper .search-panel {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #F7F7F7;
  border-radius: 20px;
  padding: 8px 15px;
}
.appbar-wrapper .search-panel i {
  color: #949494;
  font-size: 18px;
}
.appbar-wrapper .search-panel input {
  -webkit-box-flex: 1;
  -webkit-flex: 1 1;
      -ms-flex: 1 1;
          flex: 1 1;
  padding-left: 5px;
  font-size: 14px;
  background-color: #F7F7F7;
}
.appbar-wrapper .search-panel input::-webkit-input-placeholder {
  color: #949494;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
